.Addproduct {
  width: 100%;
  height: 100vh;
  align-items: center;
}

.MainAddPdt {
  background-color: rgba(255, 255, 255, 0.496);
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: rgb(9, 9, 121, 0.5);
  justify-content: space-evenly;
  width: 69%;
  height: 75%;
  border-radius: 10px;
  margin: auto;
  margin-top: 3vh;
}

.MainAddPdt > input {
  width: 40%;
  border-radius: 10px;
  border: none;
  height: 10%;
  background-color: rgba(5, 5, 71, 0.5);
  text-indent: 3%;
  color: white;
  font-size: 1.5rem;
}

#submitbtn {
  width: 20%;
  height: 10%;
  margin-top: 3vh;
  text-align: center;
  background-color: rgb(9, 9, 121, 0.5);
  border: none;
}

@media screen and (max-width: 1000px) {
  .MainAddPdt > input {
    width: 80%;
  }
}

@media screen and (max-width: 800px) {
  .MainAddPdt {
    width: 90%;
  }
  #submitbtn {
    width: 60%;
  }
}

@media screen and (max-width: 400px) {
  #submitbtn {
    width: 50%;
  }

  .MainAddPdt {
    margin-top: 4vh;
  }
}
